import React from "react";
import{BrowserRouter as Router, Route} from "react-router-dom";
import "./App.css";


import Header from "./Component/Header/Header";
import Login from "./Component/Main/Login/Login";
import Selection from "./Component/Main/Selection/Selection";
import Admin from "./Component/Main/Register/Admin/Admin";
import Mentor from "./Component/Main/Register/Mentor/Mentor";
import Student from "./Component/Main/Register/Student/Student";
import AdminContent from "./Component/Main/Content/AdminContent/AdminContent";
import MentorContent from "./Component/Main/Content/MentorContent/MentorContent";
import StudentContent from "./Component/Main/Content/StudentContent/StudentContent";

function App(props) {
  return (
    <div>
      <Router>
        <Header />
        <Route  path="/" exact component={Login}/>
        <Route path="/selection" component={Selection}/>
        <Route path="/admin" component={Admin}/>
        <Route path="/mentor" component={Mentor}/>
        <Route path="/student" component={Student}/>
        <Route path="/admin-content" component={AdminContent}/>
        <Route path="/mentor-content" component={MentorContent}/>
        <Route path="/student-content" component={StudentContent}/>
      </Router>
    </div>
  );
}

export default App;
