import React, { Component } from "react";
import { Form, Button, Breadcrumb } from "react-bootstrap";
import "./Selection.css";


export default class Selection extends Component {
  constructor(props) {
    super(props);
    this.state = {
      modelType: "",
      accountType: "",
    };
  }

  changeHandler = (event) => {
    this.setState({
      [event.target.name]: event.target.value,
    });
  };

  selectionHandler = () => {
    console.log("SelectionHandler");
    console.log(this.state.accountType);
    if (this.state.accountType === "Admin") {
      console.log("matched");
      return (
        this.props.history.push("/admin")
      );
    }else if(this.state.accountType === "Mentor") {
        console.log("matched");
        return (
          this.props.history.push("/mentor")
        );
    }else if(this.state.accountType === "Student") {
        console.log("matched");
        return (
          this.props.history.push("/student")
        );
    }
  };

  render() {
    // console.log(this.state.modelType);
    // console.log(this.state.accountType);
    return (
      <div>
        <div className="Breadcrumb">
          <Breadcrumb>
            <Breadcrumb.Item active>Step1</Breadcrumb.Item>
            <Breadcrumb.Item>Step2</Breadcrumb.Item>
          </Breadcrumb>
        </div>
        <div className="selection-Container">
          <form action="">
            <Form>
              <div>
                <Form.Label>Model Type</Form.Label>
                <br />
                <select onClick={this.changeHandler} name="modelType">
                  <option value="" key="00">
                    Choose...
                  </option>
                  <option value="school" key="01">
                    School
                  </option>
                  <option value="collage" key="02">
                    Collage
                  </option>
                </select>
              </div>
              <div>
                <Form.Label>Account Type</Form.Label>
                <br />
                <select onChange={this.changeHandler} name="accountType">
                  <option value="" key="00">
                    Choose...
                  </option>
                  <option value="Admin" key="01">
                    Admin
                  </option>
                  <option value="Mentor" key="02">
                    Mentor
                  </option>
                  <option value="Student" key="03">
                    Student
                  </option>
                </select>
              </div>
              <Button
                variant="primary"
                type="button"
                onClick={this.selectionHandler}
              >
                Next
              </Button>
            </Form>
          </form>
        </div>
      </div>
    );
  }
}
